<!--version 2.0-->
<template>
  <div>
    <div class="container"
         style="position: absolute; height: 95%; width: 98%; left: 1%; z-index: 0; border: 1px solid #CCCCCC; border-radius: 8px; overflow: hidden">
      <div id="3d-graph" style="z-index: 0">
      </div>
    </div>


    <div class="el-menu"
         style="position: absolute; z-index: 3; top: 5%; left: 3%; background-color: transparent; border-right: transparent; align-items: center">
      <el-row style="margin-bottom: 30%">
        <el-button icon="el-icon-menu" @click="menuShow" type="danger" circle></el-button>
      </el-row>

      <el-row style="margin-bottom: 30%; z-index: 3">
        <el-collapse-transition>
          <el-button v-if="showMenu" icon="el-icon-search" type="danger" class="search-node-button"
                     @click="menuSelect(0)"
                     circle></el-button>
        </el-collapse-transition>
      </el-row>

      <el-collapse-transition>
        <div v-if="showMenuItem[0].show" class="search-node-detail"
             style="position: absolute; z-index: 2; top: 17%; background-color: transparent; left: 150%; width: 1000%; text-align: left">
          <el-row style="margin-bottom: 0">
            <el-col :span="12">
              <el-autocomplete placeholder="请输入查询节点" v-model="queryA" :fetch-suggestions="querySearch"
                               :trigger-on-focus="false"
                               style="position: relative">
                <el-button slot="append" icon="el-icon-search" @click="queryForA(queryA)"></el-button>
              </el-autocomplete>
            </el-col>
          </el-row>
        </div>
      </el-collapse-transition>

      <el-row style="margin-bottom: 30%">
        <el-collapse-transition>
          <el-button v-show="showMenu" icon="el-icon-share" class="ab-routine-button" type="danger"
                     @click="menuSelect(1)" circle></el-button>
        </el-collapse-transition>
      </el-row>

      <el-collapse-transition>
        <div v-if="showMenuItem[1].show" class="search-ab-detail"
             style="position: absolute; z-index: 2; top: 30%; left: 150%; width: 1000%; text-align: left">
          <el-row style="margin-bottom: 0; alignment: left">
            <el-col :span="8">

              <el-autocomplete placeholder="请输入节点A" v-model="queryA" :fetch-suggestions="querySearch"
                               :trigger-on-focus="false" @select="selectLog"
                               style="position: relative; width: 200%">
                <template slot="prepend">A</template>
              </el-autocomplete>

            </el-col>
          </el-row>
          <el-row style="margin-bottom: 0; alignment: left">
            <el-col :span="8">
              <el-collapse-transition>
                <el-autocomplete placeholder="请输入节点B" v-model="queryB" :fetch-suggestions="querySearch"
                                 :trigger-on-focus="false" @select="selectLog"
                                 style="position: relative; width: 200%">
                  <template slot="prepend">B</template>
                  <el-button slot="append" icon="el-icon-search" @click="queryForAB" class="button-find-AB"></el-button>
                </el-autocomplete>
              </el-collapse-transition>
            </el-col>
          </el-row>
        </div>
      </el-collapse-transition>

      <el-row style="margin-bottom: 30%">
        <el-collapse-transition>
          <el-button v-show="showMenu" icon="el-icon-view" class="recolor-button" type="danger" @click="menuSelect(2)"
                     circle></el-button>
        </el-collapse-transition>
      </el-row>

      <el-collapse-transition>
        <div v-if="showMenuItem[2].show" class="select-color-type"
             style="position: absolute; z-index: 2; top: 50%; left: 150%; width: 1000%; text-align: left">
          <el-checkbox-group v-model="checkColorType">
            <el-checkbox-button label="paper" @change="recolorNodes" fill=#DB515B>Paper</el-checkbox-button>
            <el-checkbox-button label="metric" @change="recolorNodes" fill=#DB515B>Metric</el-checkbox-button>
            <el-checkbox-button label="baseline" @change="recolorNodes" fill=#DB515B>Baseline</el-checkbox-button>
          </el-checkbox-group>
        </div>
      </el-collapse-transition>

      <el-row style="margin-bottom: 30%">
        <el-collapse-transition>
          <el-button v-show="showMenu" icon="el-icon-location-outline" class="reset-camera-button" type="danger"
                     @click="resetCamera" circle></el-button>
        </el-collapse-transition>
      </el-row>

      <el-row style="margin-bottom: 30%">
        <el-collapse-transition>
          <el-button v-show="showMenu" icon="el-icon-refresh" class="reset-canvas-button" type="danger"
                     @click="resetCanvas" circle></el-button>
        </el-collapse-transition>
      </el-row>
    </div>
  </div>
</template>

<script>
import ForceGraph3D from '3d-force-graph'
import 'three'
import SpriteText from "three-spritetext";
import {UnrealBloomPass} from 'three/examples/jsm/postprocessing/UnrealBloomPass';
import Vue from "vue";
import ElementUI from 'element-ui';

const neo4j = require('neo4j-driver');
Vue.use(ElementUI);

export default {
  name: 'Canvas',
  data() {
    return {
      queryA: '',
      queryB: '',
      height: 0,
      width: 0,
      cacheData: [],
      cacheDetail: {},
      le: 0,
      Graph: null,
      driver: null,
      showMenu: false,
      showMenuItem: [{show: false}, {show: false}, {show: false}, {show: false}],
      colorList: {
        institution: '#e84545',
        person: '#0099CC',
        monograph: '#FF9900',
        fund: '#00b8a9',
        stock: '#6c5b7b',
        currency: '#b61aae',

        detail: '#fcfcfc',
        paper: '#0099CC',
        conference: '#e84545',
        title: '#e84545',
        citation: '#e84545',

        metric: '#0099CC',
        listLength: '#00b8a9',
        baseline: '#EB43D2',
        paper_baseline: '#fcfcfc'
        
      },
      linkLabelColor: '#999999',
      linkColor: '#FFFFFF',
      checkColorType: ['person', 'monograph', 'institution', 'detail', 'paper', 'conference', 'metric', 'listLength', 'citation', 'baseline', 'paper_baseline'],
      uncolored: '#666666',
      loading: true
    }
  },
  mounted() {
    // Monitor the window size
    this.width = document.body.clientWidth;
    this.height = document.body.clientHeight;
    window.addEventListener('resize', () => {
      this.width = document.body.clientWidth;
      this.height = document.body.clientHeight;
      this.Graph.height(this.height).width(this.width);
    })

    this.init3DGraph()
  },
  methods: {
    init3DGraph() {
      let _this = this
      const elem = document.getElementById('3d-graph')
      _this.driver = neo4j.driver("bolt://124.221.176.99", neo4j.auth.basic("neo4j", "123456"), {encrypted: false})
      const session2 = _this.driver.session()
      if (JSON.stringify(_this.cacheDetail) === '{}') {
        const session1 = _this.driver.session()
        // 渲染节点详细信息
        session1
          .run('MATCH (n)-[r]->(m) WHERE m.type = "detail" ' +
            'RETURN { id: id(n), label: n.name, property: properties(n), degree: size((n)--()) } as source, ' +
            '{ id: id(m), label: m.name, property: properties(m), degree: size((m)--()) } as target, type(r) as relation')
          .then(function (result) {
            const nodes = {}
            const links = result.records.map(r => {
              let target = r.get('target');
              target.id = target.id.toNumber();
              nodes[target.id] = target;
              let source = r.get('source');
              source.id = source.id.toNumber();
              nodes[source.id] = source;
              return {source: source.id, target: target.id, type: r.get('relation')}
            })
            const node_list = Object.values(nodes)
            session1.close()
            // Cache the details
            _this.cacheDetail = {nodes: {}, links: {}}
            node_list.forEach(n => {
              if (n.hasOwnProperty('id')) {
                _this.cacheDetail['nodes'][n.id] = n;
              }
            })
            links.forEach(l => {
              if (_this.cacheDetail['links'].hasOwnProperty(l.source)) {
                _this.cacheDetail['links'][l.source].push(l);
              } else {
                _this.cacheDetail['links'][l.source] = [l]
              }
            })
          });
      }

      // 渲染节点一阶关系从m到n
      session2
        .run('MATCH (n)-[r]->(m) WHERE m.type <> "detail" ' +
          'RETURN { id: id(n), label: n.name, property: properties(n), degree: size((n)--()) } as source, ' +
          '{ id: id(m), label: m.name, property: properties(m), degree: size((m)--()) } as target, type(r) as relation')
        .then(function (result) {
          const nodes = {}
          const links = result.records.map(r => {
            let source = r.get('source');
            source.id = source.id.toNumber();
            nodes[source.id] = source;
            let target = r.get('target');
            target.id = target.id.toNumber();
            nodes[target.id] = target;
            return {source: source.id, target: target.id, type: r.get('relation')}
          });
          session2.close();
          const gData = {nodes: Object.values(nodes), links: links}
          // Cache the gData
          if (_this.cacheData.length === 0) {
            for (let i = 0; i < gData['nodes'].length; i++) {
              _this.cacheData.push(gData['nodes'][i])
            }
          }

          _this.Graph = ForceGraph3D().height(_this.height).width(_this.width)
          _this.Graph(elem)
            .graphData(gData)
            .d3VelocityDecay(0.75)
            .nodeColor(node => {
              return _this.colorList[node.property.type]
            })
            .nodeLabel(node => 'description' in node.property ? node.property.description : node.label)
            // .nodeRelSize(2)
            // .nodeThreeObjectExtend(true)
            .nodeThreeObject(node => {
              const sprite = new SpriteText(node.label);
              sprite.material.depthWrite = false;
              sprite.color = _this.colorList[node.property.type];
              sprite.textHeight = _this.sizeOnDegree(node);
              return sprite;
            })
            .nodeResolution(12)
            .linkThreeObjectExtend(true)
            .linkThreeObject(link => {
              // extend link with text sprite
              const sprite = new SpriteText(`${link.type}`);
              sprite.color = _this.linkLabelColor;
              sprite.textHeight = 2;
              return sprite;
            })
            .linkPositionUpdate((sprite, {start, end}) => {
              const middlePos = Object.assign(...['x', 'y', 'z'].map(c => ({
                [c]: start[c] + (end[c] - start[c]) / 2 // calc middle point
              })));
              Object.assign(sprite.position, middlePos);
            })
            .linkLabel(link => link.type)
            .linkColor(_this.linkColor)
            .linkOpacity(0.3)
            .linkDirectionalParticles(1)
            .linkDirectionalParticleColor(() => 'red')
            .linkDirectionalParticleSpeed(0.02)
            .linkDirectionalParticleWidth(1)
            .linkDirectionalParticleResolution(8)
            .onNodeHover(node => elem.style.cursor = node ? 'pointer' : null)
            .onNodeClick(node => {
              if (_this.cacheDetail['links'].hasOwnProperty(node.id)) {
                _this.cacheDetail['links'][node.id].forEach(l => {
                  const {nodes, links} = _this.Graph.graphData()
                  let adding_node = _this.cacheDetail['nodes'][l.target]
                  _this.Graph.graphData({
                    nodes: [...nodes, adding_node],
                    links: [...links, l]
                  })
                })
              }

              _this.focusOnNode(node)
              node.fx = node.x;
              node.fy = node.y;
              node.fz = node.z;
            })

          // const bloomPass = new UnrealBloomPass(8, 0.8, 1, 0.01);
          // _this.Graph.postProcessingComposer().addPass(bloomPass);

          // if (_this.neverZoomed) {
          //   _this.Graph.onEngineStop(() => _this.Graph.zoomToFit(400, -20))
          //   _this.neverZoomed = false
          // }

        })
        .catch(function (error) {
          console.log(error);
        });
    },

    focusOnNode(node) {
      // Aim at node from outside it
      const distance = 40;
      const distRatio = 1 + distance / Math.hypot(node.x, node.y, node.z);
      this.Graph.cameraPosition(
        {x: node.x * distRatio, y: node.y * distRatio, z: node.z * distRatio}, // new position
        node, // lookAt ({ x, y, z })
        3000  // ms transition duration
      );
    },

    sizeOnDegree(node) {
      const degree = node.degree.toNumber()
      return degree >= 5 ? 7 : degree >= 2 ? 5.5 : 3.5
      //return degree >= 5 ? 3.5 : degree >= 2 ? 5.5 : 7

    },

    querySearch(queryString, cb) {
      let suggestions = JSON.parse(JSON.stringify(this.cacheData.map(elem => elem.property)))
      suggestions = suggestions.map(elem => {
        elem.value = elem.name
        return elem
      })
      let results = queryString ? suggestions.filter(this.createFilter(queryString)) : suggestions;
      // console.log(JSON.parse(JSON.stringify(results)))
      cb(JSON.parse(JSON.stringify(results)));
    },

    createFilter(queryString) {
      return (result) => {
        return (result.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },

    queryForA(queryString) {
      let valid = false
      if (queryString.length === 0) {
        this.$message.error('请先输入关键词，再进行节点检索！')
      } else {
        for (let i = 0; i < this.cacheData.length; i++) {
          if (this.cacheData[i].label.toLowerCase() === queryString.toLowerCase()) {
            this.focusOnNode(this.cacheData[i])
            valid = true
            break
          }
        }
        if (!valid) {
          this.$message.warning('关系图谱中找不到该节点，请尝试更换搜索关键词！')
        } else {
          this.showB = true
        }
      }
    },

    async queryForAB() {
      let _this = this
      // Check for validation
      let validA = false
      let validB = false
      let jump = false
      if (_this.queryA.length === 0 || _this.queryB.length === 0) {
        this.$message.error('请先确保输节点A、B均有内容，再进行AB路径检索！')
        jump = true
      } else {
        for (let i = 0; i < this.cacheData.length; i++) {
          if (_this.cacheData[i].label.toLowerCase() === _this.queryA.toLowerCase()) {
            validA = true
            break
          }
        }
        for (let i = 0; i < this.cacheData.length; i++) {
          if (_this.cacheData[i].label.toLowerCase() === _this.queryB.toLowerCase()) {
            validB = true
            break
          }
        }
        if (!validA || !validB) {
          this.$message.warning('关系图谱中无该指定节点，请尝试更换搜索节点！')
          jump = true
        }
      }
      const elem = document.getElementById('3d-graph')
      let nodes = []
      let links = []
      const session0 = _this.driver.session()
      await session0
        .run("MATCH p=shortestpath((a{name:'" + _this.queryA + "'})-[*..8]-(b{name:'" + _this.queryB + "'})) " +
          "RETURN length(p) as l")
        .then(function (result) {
          result.records.map(r => {
            _this.le = r.get("l").toNumber()
          })
          _this.le += 1
        })
        .catch(err => console.log(err))
      if (!jump) {
        const session1 = _this.driver.session()
        console.log("MATCH p=(a{name:'" + _this.queryA + "'})-[*.." + _this.le + "]-(b{name:'" + _this.queryB + "'}) " +
          "UNWIND NODES(p) as n WITH DISTINCT n RETURN { id: id(n), label: n.name, property: properties(n), degree: size((n)--()) } as nodes")
        await session1
          .run("MATCH p=(a{name:'" + _this.queryA + "'})-[*.." + _this.le + "]-(b{name:'" + _this.queryB + "'}) " +
            "UNWIND NODES(p) as n WITH DISTINCT n RETURN { id: id(n), label: n.name, property: properties(n), degree: size((n)--()) } as nodes")
          .then(function (result) {
            if (result.records.length === 0) {
              _this.$message.warning('这两个节点之间似乎没有什么关系呢~')
              jump = true
            } else {
              nodes = result.records.map(r => {
                let tmp = r.get("nodes")
                tmp.id = tmp.id.toNumber()
                return tmp
              })
            }
            session1.close()
          })

        if (!jump) {
          const session2 = _this.driver.session()
          await session2
            .run("MATCH p=(a{name: '" + _this.queryA + "'})-[*.." + _this.le + "]-(b{name: '" + _this.queryB + "'}) " +
              "UNWIND relationships(p) as r " +
              "WITH DISTINCT r RETURN r")
            .then(function (result) {
              links = result.records.map(r => {
                let link = r.get("r")
                return {
                  source: link.start.toNumber(),
                  target: link.end.toNumber(),
                  type: link.type
                }
              })

              // Clear the graph layer
              _this.Graph(elem).refresh()
              _this.Graph = ForceGraph3D().height(_this.height).width(_this.width)

              const gData = {nodes: nodes, links: links}
              _this.Graph(elem)
                .graphData(gData)
                .d3VelocityDecay(0.75)
                .nodeColor(node => {
                  return _this.colorList[node.property.type]
                })
                .nodeLabel(node => 'description' in node.property ? node.property.description : node.label)
                .nodeThreeObject(node => {
                  const sprite = new SpriteText(node.label);
                  sprite.material.depthWrite = false;
                  sprite.color = _this.colorList[node.property.type];
                  sprite.textHeight = _this.sizeOnDegree(node);
                  return sprite;
                })
                .nodeResolution(12)
                .linkThreeObjectExtend(true)
                .linkThreeObject(link => {
                  // extend link with text sprite
                  const sprite = new SpriteText(`${link.type}`);
                  sprite.color = _this.linkLabelColor;
                  sprite.textHeight = 2.5;
                  return sprite;
                })
                .linkPositionUpdate((sprite, {start, end}) => {
                  const middlePos = Object.assign(...['x', 'y', 'z'].map(c => ({
                    [c]: start[c] + (end[c] - start[c]) / 2 // calc middle point
                  })));
                  Object.assign(sprite.position, middlePos);
                })
                .linkLabel(link => link.type)
                .linkColor(_this.linkColor)
                .linkOpacity(0.3)
                .linkDirectionalParticles(1)
                .linkDirectionalParticleColor(() => 'red')
                .linkDirectionalParticleSpeed(0.02)
                .linkDirectionalParticleWidth(1)
                .linkDirectionalParticleResolution(8)
                .onNodeHover(node => elem.style.cursor = node ? 'pointer' : null)
                .onNodeClick(node => {
                  _this.focusOnNode(node)
                  node.fx = node.x;
                  node.fy = node.y;
                  node.fz = node.z;
                })

              // const bloomPass = new UnrealBloomPass(8, 0.8, 1, 0.01);
              // _this.Graph.postProcessingComposer().addPass(bloomPass);

              _this.Graph.onEngineStop(() => _this.Graph.zoomToFit(400, 10))
              session2.close()
            })
        }
      }
    },

    selectLog(item) {
      console.log(item)
    },

    menuShow(e) {
      this.showMenu = !this.showMenu
      let checkSum = 0
      this.showMenuItem.forEach(elem => {
        checkSum += elem.show
      })
      if (!this.showMenu && checkSum > 0) {
        this.showMenuItem.map(elem => {
          elem.show = false
        })
      }

      // Deliberately Blur the button if the Menu is closed
      if (!this.showMenu) {
        e.target.blur()
      }
    },

    menuSelect(show_this) {
      this.showMenuItem[show_this].show = !this.showMenuItem[show_this].show
      let checkSum = 0
      this.showMenuItem.forEach(elem => {
        checkSum += elem.show
      })
      if (checkSum > 1) {
        for (let i = 0; i < this.showMenuItem.length; i++) {
          if (i !== show_this) {
            this.showMenuItem[i].show = false
          }
        }
      }
    },

    recolorNodes() {
      let _this = this
      _this.Graph
        .nodeThreeObject(node => {
          const sprite = new SpriteText(node.label);
          sprite.material.depthWrite = false;
          if (_this.checkColorType.indexOf(node.property.type) > -1) {
            sprite.color = _this.colorList[node.property.type];
          } else {
            sprite.color = _this.uncolored;
          }
          sprite.textHeight = _this.sizeOnDegree(node);
          return sprite;
        })
    },

    resetCamera() {
      this.Graph.zoomToFit(400, -20)
      this.$message({
        message: '相机已复位！',
        type: 'success'
      })
    },

    resetCanvas() {
      const _this = this
      const elem = document.getElementById('3d-graph')
      let nodes = {};
      let links = {};
      const session = _this.driver.session()
      session
        .run('MATCH (n)-[r]->(m) WHERE m.type <> "detail" ' +
          'RETURN { id: id(n), label: n.name, property: properties(n), degree: size((n)--()) } as source, ' +
          '{ id: id(m), label: m.name, property: properties(m), degree: size((m)--()) } as target, type(r) as  relation')
        .then(function (result) {
          links = result.records.map(r => {
            let source = r.get('source');
            source.id = source.id.toNumber();
            nodes[source.id] = source;
            let target = r.get('target');
            target.id = target.id.toNumber();
            nodes[target.id] = target;
            return {source: source.id, target: target.id, type: r.get('relation')}
          })
          session.close()

          // Clear the graph layer
          let gData = {nodes: [], links: []}

          _this.Graph(elem).refresh()
          _this.Graph = ForceGraph3D().height(_this.height).width(_this.width)

          gData = {nodes: Object.values(nodes), links: links}
          _this.Graph(elem)
            .graphData(gData)
            .d3VelocityDecay(0.75)
            .nodeColor(node => {
              return _this.colorList[node.property.type]
            })
            .nodeLabel(node => 'description' in node.property ? node.property.description : node.label)
            .nodeThreeObject(node => {
              const sprite = new SpriteText(node.label);
              sprite.material.depthWrite = false;
              sprite.color = _this.colorList[node.property.type];
              sprite.textHeight = _this.sizeOnDegree(node);
              return sprite;
            })
            .nodeResolution(12)
            .linkThreeObjectExtend(true)
            .linkThreeObject(link => {
              // extend link with text sprite
              const sprite = new SpriteText(`${link.type}`);
              sprite.color = _this.linkLabelColor;
              sprite.textHeight = 2.5;
              return sprite;
            })
            .linkPositionUpdate((sprite, {start, end}) => {
              const middlePos = Object.assign(...['x', 'y', 'z'].map(c => ({
                [c]: start[c] + (end[c] - start[c]) / 2 // calc middle point
              })));
              Object.assign(sprite.position, middlePos);
            })
            .linkLabel(link => link.type)
            .linkColor(_this.linkColor)
            .linkOpacity(0.3)
            .linkDirectionalParticles(1)
            .linkDirectionalParticleColor(() => 'red')
            .linkDirectionalParticleSpeed(0.02)
            .linkDirectionalParticleWidth(1)
            .linkDirectionalParticleResolution(8)
            .onNodeHover(node => elem.style.cursor = node ? 'pointer' : null)
            .onNodeClick(node => {
              const {nodes, links} = _this.Graph.graphData()
              console.log(_this.cacheDetail)
              if (_this.cacheDetail['links'].hasOwnProperty(node.id)) {
                _this.cacheDetail['links'][node.id].forEach(l => {
                  const {nodes, links} = _this.Graph.graphData()
                  let adding_node = _this.cacheDetail['nodes'][l.target]
                  _this.Graph.graphData({
                    nodes: [...nodes, adding_node],
                    links: [...links, l]
                  })
                })
              }
              _this.focusOnNode(node)
              node.fx = node.x;
              node.fy = node.y;
              node.fz = node.z;
            })

          // const bloomPass = new UnrealBloomPass(8, 0.8, 1, 0.01);
          // _this.Graph.postProcessingComposer().addPass(bloomPass);
        })

      _this.$message({
        message: '画布已复原！',
        type: 'success'
      })
    }
  }
}
</script>

<style type="text/css">
html, body {
  height: 100%;
}

.el-button.is-circle {
  color: #DB515B !important;
  background-color: #FFF !important;
  border-color: #DCDFE6 !important;
}

.el-button.is-circle:hover {
  color: #ec7880 !important
}

.el-button.is-circle:focus {
  color: #ec7880 !important;
  border-color: #ec7880 !important;
  border-width: 1px !important;
}

.el-checkbox-button.is-checked .el-checkbox-button__inner {
  color: #FFF !important;
  background-color: #DB515B !important;
  border-color: #DB515B !important;
  box-shadow: -2px 0 0 0 #FFF !important;
}

.el-checkbox-button:first-child .el-checkbox-button__inner {
  box-shadow: none !important;
}

.el-checkbox-button.is-checked .el-checkbox-button__inner:hover {
  border-color: #ec7880 !important;
  background-color: #ec7880 !important;
}

.el-checkbox-button__inner:focus {
  border-color: #FFFFFF !important;
}

.el-checkbox-button__inner:hover {
  color: #ec7880 !important;
  border-color: #ec7880 !important;
}

.el-checkbox-button__inner {
  color: #000000 !important;
  border-color: #DB515B !important;
  border-width: 2px !important;
}
</style>



